// External
import React, { useState, useEffect } from 'react';

// Material UI
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';

// Owned
import { useStyles } from '../styles';
import HorizontalLogo from '../HorizontalLogo';
import { Request, Messages } from '../../../../shared';

import { useTranslation } from 'react-i18next';

export default function Welcome() {
    const classes = useStyles();
    const { t } = useTranslation();

    const [version, setVersion] = useState('');
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        async function getVersion() {
            const result = await new Request(Messages.Generic.GetVersion).sendRequest();
            const version = result.message.payload.version;
            setVersion(version);
            setLoading(false);
        }
        
        try {
            getVersion();
        } catch(e) {
            setVersion("");
            setLoading(false);
        }
    }, []);

    return(
        <Container component="main" maxWidth="md">
            <CssBaseline />
            {loading ?
                <div className={classes.paper}>
                    <CircularProgress />
                </div>
            :
                <div className={classes.welcomeHeader}>
                    <div className={classes.imgHeader}>
                        <HorizontalLogo light={true} width={"300px"} />
                    </div>
                    <Typography className={classes.testExtensionTitle} component="h2" variant="h5">
                        {t("update.welcome.title", { version: version })}
                    </Typography>
                    <br />
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                        <Typography className={classes.testExtensionSubtitle} component="h1" variant="h6">
                            {t("update.welcome.description")}
                        </Typography>
                        <Box>
                            <Typography variant="body" sx={{
                                color: "lightGrey",
                                marginTop: "20px"
                            }}>
                                <ul>
                                    <li>Minor bug fixes</li>
                                </ul>
                            </Typography>
                        </Box>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography className={classes.testExtensionSubtitle} component="h1" variant="h6">
                                {t("update.important_info.title")}
                            </Typography>
                            <Box>
                                <Typography variant="body" sx={{
                                    color: "lightGrey",
                                    marginTop: "20px"
                                }}>
                                    We want to inform you that starting March 13th, we will begin charging our iOS users a small fee of $1.15/month.
                                    This will allow us to keep maintaining Super Agent and continue improving it. The desktop version will remain free to use.
                                    So, for less than the price of a cup of coffee, youll be able to keep enjoying an upgraded cookie-free browsing experience online. 
                                    <br />
                                    We understand that not everyone will want to pay the subscription and we respect that. We will still provide the iOS app for free, limiting its use to three cookie pop-ups per day.
                                    When the update is launched, youll only need to open the Super Agent app in your iPhone, make sure you are logged in and enable the subscription.
                                    <br />
                                    We value and appreciate your support and trust throughout the years. 
                                    If you have any questions, please do not hesitate to contact us.
                                    
                                    <br />
                                    Thank you for using our extension.
                                    <br />
                                    Best regards,
                                    <br />
                                    Francisco Castro, CEO
                                </Typography>
                            </Box>
                        </Grid>
                        {/*
                        <Grid item xs={12}>
                            <Typography className={classes.testExtensionSubtitle} component="h1" variant="h6">
                                {t("update.donate.title")}
                            </Typography>
                            <Box>
                                <Typography variant="body" sx={{
                                    color: "lightGrey",
                                    marginTop: "20px"
                                }}>
                                    {t("update.donate.description")}
                                </Typography>
                            </Box>
                        </Grid>
                        <Grid item xs={12}>
                            <div style={{textAlign: "center"}}>
                                <Button 
                                    variant="contained"
                                    color="secondary"
                                    onClick={() => {
                                        window.open("https://donate.super-agent.com/", "_blank");
                                    }}
                                >
                                    {t("update.donate.button")}
                                </Button>
                            </div>
                        </Grid>
                        */}
                    </Grid>
                </div>            
            }

        </Container>
    )
}
